/*--------------------------------------------------------------
# Theme Styles and Layout
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Blocks - Gutenberg
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Columns
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Columns
--------------------------------------------------------------*/
.entry-content > *,
.entry-summary > * {
  max-width: 100%; }

/*--------------------------------------------------------------
## Images
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Cover Image
--------------------------------------------------------------*/
.wp-block-cover.alignwide {
  width: 75vw; }

/*--------------------------------------------------------------
#  Image
--------------------------------------------------------------*/
.wp-block-image::after {
  display: block;
  clear: both;
  content: ""; }

/*--------------------------------------------------------------
## Blockquotes & Pullquotes
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Blockquotes & Pull Quotes
--------------------------------------------------------------*/
.wp-block-quote {
  border-left: 2px solid #E31837;
  padding-left: 1rem; }
  .wp-block-quote cite {
    font-size: 80%;
    color: #646464; }
  .wp-block-quote.is-style-large {
    border-left: 0;
    padding-left: 0; }

.wp-block-pullquote {
  border-top: 3px solid #646464;
  border-bottom: 3px solid #646464; }
  .wp-block-pullquote.is-style-solid-color {
    border: 0; }

/*--------------------------------------------------------------
## Gallery
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Gallery
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Tables
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Tables
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Separators
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Separators
--------------------------------------------------------------*/
.wp-block-separator {
  background-color: #646464;
  border: 0;
  height: 2px;
  margin-bottom: 2rem;
  margin-top: 2rem;
  max-width: 10rem;
  text-align: center; }
  .wp-block-separator.is-style-wide {
    max-width: 100%; }

/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Buttons
--------------------------------------------------------------*/
.wp-block-button__link:hover {
  filter: brightness(85%);
  text-decoration: none; }

/*--------------------------------------------------------------
## Embeds
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Embeds
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Shared Block Styles
--------------------------------------------------------------*/
.alignleft {
  float: left; }

.alignright {
  float: right; }

.aligncenter {
  display: flex;
  align-items: center;
  justify-content: center; }

.alignwide {
  margin-left: calc(25% - 25vw);
  margin-right: calc(25% - 25vw);
  max-width: 100vw; }

.alignfull {
  width: 100vw;
  max-width: 100vw;
  margin-left: calc(50% - 50vw); }

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comments-area {
  margin-top: 2rem; }

.comment-content a {
  word-wrap: break-word; }

.bypostauthor {
  display: block; }

/*--------------------------------------------------------------
## Posts and Pages
--------------------------------------------------------------*/
.sticky {
  display: block; }

.byline,
.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.post {
  margin-bottom: 4rem;
  padding-bottom: 1rem; }

.entry-title a {
  color: #2B2D42; }
  .entry-title a:hover {
    text-decoration: none;
    color: #495057; }

.entry-content h2 {
  color: #E31837; }

.entry-content > p:first-child {
  color: #E31837;
  font-size: 1.6rem; }

.post-thumbnail {
  margin-bottom: 1rem; }
  .post-thumbnail.alignfull {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
    .post-thumbnail.alignfull img {
      height: 100%;
      left: 0;
      object-fit: cover;
      top: 0;
      transform: none;
      width: 100%;
      margin-top: -3rem; }
  .post-thumbnail__link img {
    box-shadow: 6px 6px 0 #E31837; }

.entry-meta,
.entry-footer {
  font-size: 0.75rem;
  margin: 0.75rem 0 1rem; }
  .entry-meta .svg-inline--fa,
  .entry-footer .svg-inline--fa {
    margin-right: 0.3rem; }
  .entry-meta span,
  .entry-footer span {
    margin: 0 1.5rem 0.4rem 0;
    display: inline-block; }
    .entry-meta span:last-child,
    .entry-footer span:last-child {
      margin: 0; }
    .entry-meta span a,
    .entry-footer span a {
      text-decoration: none;
      color: #646464; }
      .entry-meta span a:hover,
      .entry-footer span a:hover {
        color: #2B2D42; }
  .entry-meta .edit-link .btn,
  .entry-footer .edit-link .btn {
    color: #fff; }
    .entry-meta .edit-link .btn:hover,
    .entry-footer .edit-link .btn:hover {
      color: #fff; }

/* Posts Pagination */
.pagination {
  justify-content: center; }
  .pagination .page-numbers {
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding: 0.5rem;
    border-radius: 0.25rem;
    margin: 0 0.2rem;
    width: 2rem;
    text-align: center; }
    .pagination .page-numbers.current, .pagination .page-numbers:hover {
      background-color: #E31837;
      color: white;
      text-decoration: none; }

/* Posts Navigation */
.nav-links {
  clear: both;
  display: flex;
  justify-content: space-between; }

/* Screenreaders */
.screen-reader-text {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

/* 404 page */
.error404 .content-area {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  flex: 0 0 100%;
  max-width: 100%; }

.posts-latest__post-date {
