.image-box {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative; }
  .image-box__content {
    position: relative;
    z-index: 100; }
    .image-box__content h4 {
      margin-bottom: 0; }
  .image-box__inner {
    align-items: center;
    cursor: hand;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    opacity: 1;
    overflow: hidden;
    padding: 15px;
    position: absolute;
    top: 0;
    transition: 0.3s ease all;
    width: 100%; }
    .image-box__inner-overlay {
      transition: 0.3s ease all; }
    .image-box__inner-icon {
      bottom: 0;
      right: 0;
      z-index: 50;
      transition: 0.3s ease all; }
    .image-box__inner:before {
      background-color: transparent;
      content: "";
      height: 100%;
      position: absolute;
      transition: 0.3s ease all;
      width: 100%;
      z-index: 99; }
    .image-box__inner:hover:before {
      background-color: rgba(49, 49, 49, 0.5); }
    .image-box__inner.hasOverlay:hover:before {
      background-color: transparent; }
